<template>
	<view>
		<view class="news-detail">
			<view class="detail-title">{{detail.newsTitle}}</view>
			<view class="detail-extra">
				<uni-dateformat :date="detail.createTime" :format="'yyyy-MM-dd hh:mm'"></uni-dateformat>
				<text class="category">{{detail.newsCategoryName}}</text>
			</view>
			<view class="detail-content">
				<rich-text :nodes="detail.newsContent"></rich-text>
			</view>
			<u-divider class="end-line" text="结束了" :dashed="true"></u-divider>
			<u-gap height="60"></u-gap>
		</view>
		
		<u-back-top :scrollTop="scrollTop" :mode="'circle'" :bottom="80" :right="20" :top="400" 
			:icon="'arrow-upward'" :icon-style="iconStyle">
		</u-back-top>
	</view>
</template>

<script>
export default {
	data() {
		return {
			scrollTop: 0,
			iconStyle: { color: '#909399', fontSize: '38rpx' },
			
			detail: { },
			
			params: { },
		};
	},
	onLoad(options) {
		this.loadData(options.newsId);
	},
	methods: {
		// 打开页面
		async loadData(newsId) {
			//detail
			const detailRes = await this.$http({url: '/wechat/news/' + newsId, data: this.params});
			detailRes.data.newsContent = detailRes.data.newsContent.replace(/<img/gi, '<img width="100%!important" ');
			this.detail = detailRes.data;
		},
		// 返回
		goBack() {
			uni.navigateBack();
		},
		//返回顶部
		onPageScroll(e) {
			this.scrollTop = e.scrollTop;
		}
	}
};
</script>

<style lang="scss">
.news-detail {
	background-color: #ffffff;
	.detail-title {
		border-left: 10rpx solid #2175F6;
		padding: 0 30rpx;
		font-size: $uni-font-size-title;
		font-weight: bold;
		color: $uni-color-title;
		line-height: 1.5;
	}
	.detail-extra {
		padding: 20rpx 30rpx;
		font-size: 24rpx;
		color: $uni-text-color-grey;
		.category{
			padding-left: 40rpx;
		}
	}
	.detail-content {
		padding: 30rpx 30rpx;
		font-size: $uni-font-size-paragraph;
		color: $uni-color-paragraph;
		line-height: 2;
	}
	.end-line{
		padding: 0 20rpx 0 20rpx;
	}
}
</style>
